<template>
	<div id="statcenter-takeout-order">
		<public-header title="店铺订单统计"></public-header>
		<div class="content padding-15 font-14 ">
			<van-row class="c-gray">
				<van-col span="3" class="padding-10-tb">排名</van-col>
				<van-col span="6" class="padding-10-tb">商户</van-col>
				<van-col span="4" class="padding-10-tb">营业额</van-col>
				<van-col span="4" class="padding-10-tb">总收入</van-col>
				<van-col span="4" class="padding-10-tb">有效单量</van-col>
				<van-col span="3" class="padding-10-tb">无效单量</van-col>
			</van-row>
			<van-row>
				<van-col span="3">--</van-col>
				<van-col span="6">总计</van-col>
				<van-col span="4">￥{{total.final_fee}}</van-col>
				<van-col span="4">￥{{total.store_final_fee}}</van-col>
				<van-col span="4">{{total.total_success_order}}</van-col>
				<van-col span="3">{{total.total_cancel_order}}</van-col>
			</van-row>
			<van-row v-for="(item, index) in stat" :key="index" v-if="stat">
				<van-col span="3">{{index + 1}}</van-col>
				<van-col span="6">{{item.store_name}}</van-col>
				<van-col span="4">￥{{item.final_fee}}</van-col>
				<van-col span="4">￥{{item.store_final_fee}}</van-col>
				<van-col span="4">{{item.total_success_order}}</van-col>
				<van-col span="3">{{item.total_cancel_order}}</van-col>
			</van-row>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState} from 'vuex'
	import publicHeader from "@/components/header"
	import {getData} from "../../controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				stat: [],
				total: [],
				store: {},
				orderby: {
					'final_fee': '营业额',
					'total_success_order': '有效订单量',
					'store_final_fee': '总收入'
				}
			}
		},
		methods: {
			onLoad() {
				let that = this;
				getData({
					vue: this,
					data: {
						detail: 1,
						filter: that.filter ? that.filter.items: {}
					},
					url: 'plateform/statcenter/takeoutOrder/index',
					success: function(result) {
						that.stat = result.stat;
						that.total = result.total;
						that.store = result.store;
					}
				});
			}
		},
		computed: {
			...mapState([
				'filter',
			]),
		},
		mounted() {
			this.onLoad();
		},

	}
</script>

<style scoped>
	#statcenter-takeout-order .van-row{
		font-size: 12px;
		text-align: center;
	}
	#statcenter-takeout-order .van-row:nth-child(odd) {
		background-color: #ddd;
	}
	#statcenter-takeout-order .van-row .van-col{
		padding: 8px 0;
		border-right: 1px solid #eee;
	}
	#statcenter-takeout-order .van-row .van-col:last-child{
		border-right: 0px;
	}
</style>


